export const convertShipmentApiResponseToLocal = (shipment) => {
  return {
    ...shipment,
    transportationList: convertTransporationToLocal(shipment),
    additionalDetails: convertAdditionalDetailsToLocal(shipment),
    rateShopList: shipment.rateShopDetails?.map((row) => ({
      ...row,
      accessorials: row.freightAccessorialCollection
        ? row.freightAccessorialCollection?.map((row) => ({
            ...row,
            description: row.costName,
            auditedChargeAmount: row.cost,
            currencyCode: row.currency,
          }))
        : [],
    })),
    shipmentVendorList: shipment.shipmentVendorDetails,
    shipmentBillingDetails: shipment.shipmentBillToInfo,
    shipmentFilesList: shipment.shipmentDocuments.map((row) => ({
      renderKey: uniqueId(),
      ...row,
    })),
    shipmentCategoryList: shipment.hierarchialDetails,
    taxes: shipment.shipmentTaxes,
  };
};
export const convertTransporationToLocal = (shipment) => {
  let localData = [];
  for (const key in SHIPMENT_TRANSPORTATION_OBJECTS) {
    let value = SHIPMENT_TRANSPORTATION_OBJECTS[key];
    if (Object.hasOwnProperty.call(shipment, value)) {
      let rows = shipment[value];
      rows?.forEach((row) => {
        let data = {};
        data.transportationType =
          SHIPMENT_TRANSPORTATION_TYPES[SHIPMENT_TRANSPORTATION_OBJECTS[key]];

        const TYPE_NO =
          SHIPMENT_TRANSPORTATION_TYPES[SHIPMENT_TRANSPORTATION_OBJECTS[key]];
        const TYPE =
          TYPE_NO ===
          SHIPMENT_TRANSPORTATION_TYPES[SHIPMENT_TRANSPORTATION_OBJECTS.PICKUP]
            ? { short: "shipper", additional: "originAdditionalInfo" }
            : { short: "consignee", additional: "destinationAdditionalInfo" };

        data.stopType = valueToString(
          SHIPMENT_TRANSPORTATION_TYPES[SHIPMENT_TRANSPORTATION_OBJECTS[key]]
        );
        data.appointmentNo = row[`${TYPE.short}ReferenceNo`];
        data.primaryKey = row[`${TYPE.short}Key`];
        data.appointmentType = row[`${TYPE.short}AppointmentType`];
        data.requestedDate = convertToMoment(
          row[TYPE.additional][
            `${
              TYPE_NO === 1
                ? "shipperRequestedShipDate"
                : "shipperRequestedDeliveryDate"
            }`
          ]
        );

        data.requestedTime = convertToMoment(
          row[TYPE.additional][
            `${
              TYPE_NO === 1
                ? "shipperRequestedShipDate"
                : "shipperRequestedDeliveryDate"
            }`
          ]
        );

        data.appointmentDate = convertToMoment(
          row[TYPE.additional][
            `${
              TYPE_NO === 1
                ? "carrierAppontmentShipDate"
                : "carrierAppontmentDeliveryDate"
            }`
          ]
        );

        data.appointmentTime = convertToMoment(
          row[TYPE.additional][
            `${
              TYPE_NO === 1
                ? "carrierAppontmentShipDate"
                : "carrierAppontmentDeliveryDate"
            }`
          ]
        );

        data.actualDate = convertToMoment(
          row[TYPE.additional][
            `${
              TYPE_NO === 1
                ? "carrierActualShipDate"
                : "carrierActualDeliveryDate"
            }`
          ]
        );

        data.actualTime = convertToMoment(
          row[TYPE.additional][
            `${
              TYPE_NO === 1
                ? "carrierActualShipDate"
                : "carrierActualDeliveryDate"
            }`
          ]
        );

        data = {
          ...data,
          ...row[TYPE.additional],
          ...getDecodeWeeklyDaysOperation(row.weeklyOperation),
          ...getDecodeWeeklyHoursOperation(row.hoursOperation),
        };
        data.shipmentKey = row.shipmentKey;
        data.id = row.id;
        data.legNumber = row.legNumber;
        data.sequence = valueToString(row.sequence);
        data.searchId = row.searchId;
        data.name = row.name;
        data.address1 = row.address1;
        data.address2 = row.address2;
        data.zipCode = row.zipCode;
        data.city = row.city;
        data.stateCode = row.stateCode;
        data.countryCode = row.countryCode;
        data.businessPhone = row.businessPhone;
        data.matchedBolKey = row.matchedBolKey;
        data.portName = row.portName;
        data.portType = row.portType;
        data.portCode = row.portCode;
        data.portRecKey = row.portRecKey;
        data.countryName = row.countryName;
        data.stateName = row.stateName;
        data.hrsOfOperation = row.hrsOfOperation;
        data.weeklyOperation = row.weeklyOperation;
        data.hoursOperation = row.hoursOperation;
        data.legMiles = row.legMiles;
        data.customerKey = row.customerKey;
        data.oldStopSequence = row.oldStopSequence;
        data.createdBy = row.createdBy;
        data.extension = row.extension;
        data.dockNumber = row.dockNumber;
        data.displaySequence = valueToString(data.displaySequence);
        data.gridOperation = 2;
        data.isResetNew = 0;
        data.latitude = row.latitude;
        data.longitude = row.longitude;
        data.totalWeight = row.totalWeight;
        for (const key in data) {
          if (typeof data[key] === "string") {
            data[key] = data[key].trim();
          }
        }

        localData.push(data);
        data.shipperAppointmentType = row.shipperAppointmentType;
        data.consigneeAppointmentType = row.consigneeAppointmentType;
      });
    }
  }
  return _.orderBy(localData, ["displaySequence"], ["asc"]);
};

const getDecodeWeeklyDaysOperation = (date = "") => {
  date = date ? date : "";
  let [fromOperationDay, toOperationDay] = date
    ?.split("TO")
    ?.map((ele) => (ele.trim()?.length > 0 ? ele.trim() : undefined));
  fromOperationDay =
    fromOperationDay == "undefined" ? undefined : fromOperationDay;
  toOperationDay = toOperationDay == "undefined" ? undefined : toOperationDay;
  return { fromOperationDay, toOperationDay };
};

const getDecodeWeeklyHoursOperation = (date = "") => {
  date = date ? date : "";
  const [fromOperationHours, toOperationHours] = date
    ?.split("TO")
    ?.map((ele) => (ele.trim()?.length > 0 ? ele.trim() : undefined));
  return {
    fromOperationHours: fromOperationHours
      ? convertTimetoMoment(fromOperationHours)
      : "",
    toOperationHours: toOperationHours
      ? convertTimetoMoment(toOperationHours)
      : "",
  };
};

export const convertAdditionalDetailsToLocal = (shipment) => {
  return {
    status: shipment.status,
    shipmentCost: shipment.shipmentCost,
    currencyCode: shipment.currencyCode,
    shipmentType: shipment.shipmentType,
    shipmentTerms: shipment.shipmentTerms,
    proNo: shipment.proNo,
    masterBolNumber: shipment.masterBolNumber,
    accountNumber: shipment.accountNumber,
    hazmatContactName: shipment.hazmatContactName,
    freightAllowance: shipment.freightAllowance,
    createdByName: shipment.createdByName,
    createdOn: convertDateStringtoMoment(shipment.createdOn),
    modifiedByName: shipment.modifiedByName,
    modifiedOn: convertDateStringtoMoment(shipment.modifiedOn),
    branchKey: shipment.branchKey,
    incoCode: shipment.incoCode,
    incoPoint: shipment.incoPoint,
    fobCode: shipment.fobCode,
    createdBy: valueToNull(shipment.createdBy),
    modifiedBy: valueToNull(
      shipment.modifiedBy
        ? valueToNumber(shipment.modifiedBy)
        : shipment.modifiedBy
    ),
    bolNumber: shipment.bolNumber,
    carrierKey: valueToNull(shipment.carrierKey),
    carrierName: shipment.carrierName,
    trailerNumber: shipment.trailerNumber,
    poNumber: shipment.poNumber,
    purchaseOrderNumber: shipment.purchaseOrderNumber,
    salesOrderNumber: shipment.salesOrderNumber,
    paymentTerms: shipment.paymentTerms,
    paidAmount: shipment.paidAmount,
    isMasterShipment: shipment?.isMasterShipment === "Y",
    isChildShipment: shipment?.isChildShipment === "Y",
    shipmentTermsDesc: shipment.shipmentTermsDesc,
    shipmentTypeDesc: shipment.shipmentTypeDesc,
    branchDesc: shipment.branchDesc,
  };
};

const convertToMoment = (date) => (date ? moment(date) : "");

export const getShipmentTypeCounts = (type, listData) => {
  listData = _.filter(listData, function (data) {
    return data.transportationType == type;
  });
  return listData.length;
};

