export const convertShipmentDetailsLocalToAPI = ({
  action,
  customerKey,
  masterShipment,
  ...data
}) => {
  const userInfo = localStorage.getItem("userInfo");
  const userId = (userInfo ? JSON.parse(userInfo) : {}).id;
  let newData = {
    action,
    userId,
  };

  newData["shipmentInformationRequest"] = convertAdditionalDetailsLocalToAPI(
    data.additionalDetails,
    data,
    customerKey,
    data.shipmentKey,
    data.shipmentNo,
    userId,
    data.approvalNumber,
    masterShipment
  );

  newData = {
    ...newData,
    ...convertTransportationListLocalToAPI(data.transportationList, userId),
    ...fixedData,
  };

  newData["shipmentProductInformation"] = convertProductLocalToAPI(
    data.productList,
    data.shipmentKey,
    userId
  );
  newData["shipmentBillToRequestInformation"] = convertBillLocaltoAPI(
    data.shipmentBillToInfo,
    data.shipmentKey,
    userId
  );
  newData["shipmentVendorRequestInformation"] = convertVendorLocalToAPI(
    data.shipmentVendorList,
    data.shipmentKey,
    userId
  );
  newData["shipmentDocumentInformation"] =
    convertDocumentsLocalToApiForAddShipment(data.documentList);
  newData["shipmentCategoryInformation"] = convertCategorLocalToAPI(
    data.shipmentCategoryInformation,
    data.shipmentKey,
    userId
  );
  // newData["purchaseSalesOrderInformation"] = data.purchaseSalesOrderInformation; //
  newData["shipmentAccessorialInformation"] = data.accessorials
    ? data?.accessorials?.map((row) => ({
        shipmentKey: 0,
        accChargeType: row?.codeId,
        accChargeAmount: valueToNumber(row?.accChargeAmount),
        gridOperation: 1,
        codeDesc: valueToString(row?.codeDesc),
        lookupKey: valueToNumber(row?.lookupKey),
        createdBy: userId,
        createdOn: moment(new Date()),
      }))
    : [];
  newData["shipmentTaxInformation"] = data.taxes
    ? data?.taxes?.map((row, index) => ({
        shipmentKey: 0,
        sequence: index + 1,
        taxAmount: valueToNumber(row?.taxAmount),
        taxTypeCode: valueToString(row?.taxTypeCode),
        createdBy: userId,
        createdOn: moment(new Date()),
        gridOperation: 2,
        // taxSetupKey: 0,
        // shipmentTaxId: 0,
        // taxTypeDesc: "string",
      }))
    : [];

  newData["shipmentRateShopInformation"] =
    data?.shipmentRateShopInformation?.length > 0
      ? data?.shipmentRateShopInformation
      : [];

  return newData;
};

