const convertTransportationListLocalToAPI = (data, userID, customerKey) => {
  let newData = {
    shipmentOriginInformation: [],
    shipmentDestinationInformation: [],
  };

  data?.forEach(({ stopType, portRecKey, ...row }) => {
    let params =
      stopType ==
      SHIPMENT_TRANSPORTATION_TYPES[SHIPMENT_TRANSPORTATION_OBJECTS.PICKUP]
        ? {
            key: "originKey",
            additionalInfo: "shipmentOriginAdditionalInformation",
            requestedDate: "shipperRequestedShipDate",
            actualDate: "carrierActualShipDate",
            appointmentDate: "carrierAppontmentShipDate",
          }
        : {
            key: "destinationKey",
            additionalInfo: "shipmentDestinationAdditionalInformation",
            requestedDate: "shipperRequestedDeliveryDate",
            actualDate: "carrierActualDeliveryDate",
            appointmentDate: "carrierAppontmentDeliveryDate",
          };
    newData[
      SHIPMENT_TRANSPORTATION_ADDITIONAL_INFO_POST[
        SHIPMENT_TRANSPORTATION_TYPES_ID[stopType]
      ]
    ].push({
      shipmentKey: row.shipmentKey,
      displaySequence: valueToNumber(nullToValue(row.displaySequence)),
      sequence: valueToNumber(nullToValue(row.sequence)),
      legNumber: row.legNumber,
      stopType: stopType,
      shipRecKey: row.shipRecKey,
      customerKey: customerKey,
      name: row.name,
      addr1: row.address1,
      addr2: row.addr2,
      city: row.city,
      state: row.stateCode,
      zip: row.zipCode,
      countryCode: row.countryCode,
      searchId: row.searchId,
      createdBy: nullToValue(userID),
      oldStopSequence: nullToValue(row.oldStopSequence),
      portType: row.portType,
      portRecKey: nullToValue(valueToNumber(portRecKey)),
      gridOperation: nullToValue(row.gridOperation),
      [params.additionalInfo]: {
        shipmentKey: row.shipmentKey,
        displaySequence: valueToNumber(nullToValue(row.displaySequence)),
        sequence: valueToNumber(nullToValue(row.sequence)),
        [params.requestedDate]: row.requestedDate
          ? dateTimeToSingleValue(row.requestedDate, row.requestedTime)
          : null,
        [params.appointmentDate]: row.appointmentDate
          ? dateTimeToSingleValue(row.appointmentDate, row.appointmentTime)
          : null,
        [params.actualDate]: row.actualDate
          ? dateTimeToSingleValue(row.actualDate, row.actualTime)
          : null,

        pickupDeliveryID: row.appointmentNo,
        doorDockID: row.doorDockID,
        contactName: row.contactName,
        contactEmail: row.contactEmail,
        phone: row.phone,
        createdBy: nullToValue(userID),
        hoursOfOperation: `${valueToString(
          row.fromOperationDay
        )} TO ${valueToString(
          row.toOperationDay
        )} | ${convertOperationHoursTime(
          row.fromOperationHours
        )} TO ${convertOperationHoursTime(row.toOperationHours)}`,
        appointmentType: row.appointmentType,
        gridOperation: nullToValue(row.gridOperation),
        oldStopSequence: row.oldStopSequence,
        isResetNew: row.isResetNew,
        uom: row.uom,
        legMiles: row.legMiles,
        extension: row.extension,
        dockNumber: row.dockNumber,
        taxId: row.taxId,
      },
      countryName: row.countryName,
      stateName: row.stateName,
      portName: row.portName,
      isResetNew: row.isResetNew,
    });
  });

  return newData;
};

