export const calculateSequenceAndDisplaySequence = (transportationList) => {
  const totalPickups = _.filter(transportationList, {
    sequence: "1",
  }).length;
  const totalDeliveries = _.filter(transportationList, {
    sequence: "99",
  }).length;
  // var deleteIndex = _.findIndex(transportationList, { gridOperation: 3 });

  // const originIndex = _.findIndex(transportationList, { sequence: "1" });
  const destinationIndex = _.findIndex(transportationList, { sequence: "99" });
  const newItemIndex = _.findIndex(transportationList, { isNew: true });
  let newItemSequence = 0;
  let newItemDisplaySequence =
    transportationList.length - (totalDeliveries > 0 ? 1 : 0);

  const getSequence = (index, isPickup) => {
    if (isPickup) {
      let pickupIndex = index >= 0 ? index : transportationList.length;
      return valueToNumber(transportationList[pickupIndex - 1].sequence) + 1;
    } else {
      return valueToNumber(transportationList[index - 1].sequence) + 1;
    }
  };

  if (newItemIndex >= 0) {
    if (
      transportationList[newItemIndex].stopType ==
      SHIPMENT_TRANSPORTATION_TYPES[SHIPMENT_TRANSPORTATION_OBJECTS.PICKUP]
    ) {
      newItemSequence =
        totalPickups > 0 ? getSequence(destinationIndex, true) : "1";
    } else {
      newItemSequence =
        totalDeliveries > 0 ? getSequence(destinationIndex, true) : "99";
    }
  }

  transportationList[newItemIndex] = {
    ...transportationList[newItemIndex],
    sequence: valueToString(newItemSequence),
    displaySequence: valueToString(newItemDisplaySequence),
  };

  if (totalDeliveries > 0) {
    transportationList[destinationIndex] = {
      ...transportationList[destinationIndex],
      displaySequence: valueToString(transportationList.length),
    };
  }

  // if (deleteIndex >= 0) {
  //   let count = 1;
  //   transportationList = transportationList?.map((row, index) => {
  //     if (deleteIndex !== index && destinationIndex !== index) {
  //       row.sequence = count;
  //       row.gridOperation = 2; // For modify
  //       count++;
  //     }

  //     return row;
  //   });
  // }

  // data.displaySequence = displaySequence ? displaySequence : 1;
  // data.sequence = displaySequence ? displaySequence : 1;
  let sequence = 0;
  transportationList = transportationList.map((row) => {
    if (row.sequence != 99) {
      sequence += 1;
    }
    return {
      ...row,
      sequence: row.sequence == 99 ? 99 : sequence,
    };
  });
  return transportationList;
};

export const calculateSequenceAndDisplaySequenceForEdit = (
  transportationList
) => {
  const newStopIndex = _.findIndex(transportationList, { gridOperation: 1 });

  if (newStopIndex >= 0) {
    const highestSequence = maxBy(
      filter(
        transportationList,
        ({ stopType, sequence }) =>
          stopType === transportationList[newStopIndex].stopType &&
          sequence >= 0
      ),
      ({ sequence }) => sequence
    );

    transportationList[newStopIndex].sequence =
      Number(highestSequence.sequence ? highestSequence.sequence : 0) + 1;
    transportationList[newStopIndex].displaySequence =
      transportationList?.length - 1;
  }

  return transportationList;
};

