export const prmShipmentTransportation = (data, userID, customerKey) => {
  let newData = [];

  data?.forEach(({ stopType, portRecKey, ...row }) => {
    let params =
      stopType ==
      SHIPMENT_TRANSPORTATION_TYPES[SHIPMENT_TRANSPORTATION_OBJECTS.PICKUP]
        ? {
            key: "originKey",
            requestedDate: "prmShipperRequestedShipDate",
            actualDate: "prmCarrierActualShipDate",
            appointmentDate: "prmCarrierAppontmentShipDate",
          }
        : {
            key: "destinationKey",
            requestedDate: "prmShipperRequestedDeliveryDate",
            actualDate: "prmCarrierActualDeliveryDate",
            appointmentDate: "prmCarrierAppontmentDeliveryDate",
          };
    newData.push({
      [params.requestedDate]: dateTimeToSingleValue(
        row.requestedDate,
        row.requestedTime
      ),
      [params.appointmentDate]: dateTimeToSingleValue(
        row.appointmentDate,
        row.appointmentTime
      ),
      [params.actualDate]: dateTimeToSingleValue(
        row.actualDate,
        row.actualTime
      ),
      prmShipperConsigneeKey: nullToValue(valueToNumber(row.primaryKey)), //prmShipperConsigneeKey
      // [params.key]: nullToValue(valueToNumber(row.primaryKey)), //prmShipperConsigneeKey
      prmSequence: valueToNumber(nullToValue(row.sequence)),
      prmDisplaySequence: valueToNumber(nullToValue(row.displaySequence)),
      prmLegNumber: valueToNumber(row.legNumber),
      prmPortRecKey: nullToValue(valueToNumber(portRecKey)),
      prmStopType: valueToString(stopType),
      prmOperation: valueToNumber(nullToValue(row.gridOperation)),
      prmPortType: valueToString(row.portType),
      prmDoorDockID: valueToString(row.doorDockID),
      prmPickupDeliveryID: valueToString(row.appointmentNo),
      prmContactName: valueToString(row.contactName),
      prmContactEmail: valueToString(row.contactEmail),
      prmPhone: valueToString(row.phone),
      prmHoursOfOperation: `${valueToString(
        row.fromOperationDay
      )} TO ${valueToString(row.toOperationDay)} | ${convertOperationHoursTime(
        row.fromOperationHours
      )} TO ${convertOperationHoursTime(row.toOperationHours)}`,
      prmAppointmentType: valueToString(row.appointmentType),
      prmUom: valueToString(row.uom),
      prmLegMiles: valueToNumber(row.legMiles),
      prmDockNumber: valueToString(row.dockNumber),
      prmExtension: valueToString(row.extension),
      prmTaxId: valueToString(row.taxId),
      customerKey: valueToNumber(customerKey),
      name: valueToString(row.name),
      addr1: valueToString(row.address1),
      addr2: "",
      city: valueToString(row.city),
      state: valueToString(row.stateCode),
      zip: valueToString(row.zipCode),
      countryCode: valueToString(row.countryCode),
      searchId: valueToString(row.searchId),
      prmModifiedBy: nullToValue(userID),
      isChangeLocationDetails: valueToNumber(row.isChangeLocationDetails),
    });
  });
  return newData;
};

