export const parseQuoteDataToRateEngine = (data) => {
  let shipmentOrigin = filter(data?.transportGridModel, { stopTypeID: "1" });
  shipmentOrigin = shipmentOrigin?.[0];
  let shipmentDestination = filter(data?.transportGridModel, {
    stopTypeID: "2",
  });
  shipmentDestination = shipmentDestination?.[shipmentDestination?.length - 1];

  const totalNumberOfPieces = sumBy(data?.productGridModel, ({ billQty }) =>
    valueToNumber(billQty)
  );

  const totalWeight = sumBy(data?.productGridModel, ({ weight }) =>
    valueToNumber(weight)
  );

  let totalClass = maxBy(data?.productGridModel, ({ class: classValue }) =>
    valueToNumber(classValue)
  )?.class;

  let formData = {
    RateShopCriteria: {
      Accessorials: [
        ...data?.accessorials?.map((row) => ({
          AccessorialCode: row.accChargeType?.trim(),
          Description: row.codeDesc,
          BilledAmount: 0,
          CurrencyCode: null,
        })),
      ],
      Details: filter(data.productGridModel, ({ weight }) => weight > 0).map(
        (product, index) => ({
          SequenceNumber: valueToNumber(index + 1),
          NumberOfPieces: valueToNumber(product.billQty),
          ActualWeight: 0,
          BilledWeight: valueToNumber(product.weight),
          BilledWeightUOM: null,
          RatingClass: valueToString(product.class),
          MeasurementType: valueToString(product.weightUom),
          BilledQuantity: valueToNumber(product.billQty),
          Length: valueToNumber(product.length),
          Width: valueToNumber(product.width),
          Height: valueToNumber(product.height),
          // Cube: valueToNumber(product.cube),
          Cube: 0,
          CubeUOM: null,
          ProductCode: "",
          TrailerKey: 0,
        })
      ),
      Lanes: [
        {
          Id: 1,
          LaneKey: 0,
          Origin: {
            Port: {
              Key: valueToNumber(shipmentOrigin?.portRecKey),
              Type: stringToNull(shipmentOrigin?.portType),
              Code: stringToNull(shipmentOrigin?.portCode),
            },
            Key: valueToNumber(shipmentOrigin?.primaryKey),
            Address1: valueToString(shipmentOrigin?.address1),
            Address2: null,
            City: shipmentOrigin?.city,
            StateCode: shipmentOrigin?.state,
            CountryCode: shipmentOrigin?.countryCode,
            Postal: shipmentOrigin?.postalCode,
            PostalTo: null,
            BranchKey: -1, // fixed
          },
          Destination: {
            Port: {
              Key: valueToNumber(shipmentDestination?.portRecKey),
              Type: stringToNull(shipmentDestination?.portType), // -
              Code: stringToNull(shipmentDestination?.portCode), // -
            },
            Key: valueToNumber(shipmentDestination?.primaryKey),
            Address1: valueToString(shipmentDestination?.address1), // -
            Address2: null,
            City: shipmentDestination?.city, // -
            StateCode: shipmentDestination?.state,
            CountryCode: shipmentDestination?.countryCode, //-
            Postal: shipmentDestination?.postalCode, // zipCode
            PostalTo: null,
            BranchKey: -1, // fixed
          },
          MileageMile: 0, // Have to Check ---------------------------
        },
      ],
      Stops:
        data?.transportGridModel?.length > 2
          ? data?.transportGridModel?.map((row, index) => ({
              LaneId: 1,
              Sequence: valueToNumber(index + 1),
              Type: null,
              ShipRecKey: 0,
              Location: {
                Port: {
                  Key: valueToNumber(row?.portRecKey),
                  Type: stringToNull(row?.portType),
                  Code: stringToNull(row?.portCode),
                },
                Key: 0,
                Address1: valueToString(row.address1),
                Address2: valueToString(row.address2),
                City: valueToString(row.city),
                StateCode: valueToString(row.state),
                CountryCode: valueToString(row.countryCode),
                Postal: valueToString(row.postalCode),
                PostalTo: null,
                BranchKey: -1, // fixed
              },
              NumberPieces: totalNumberOfPieces, // static
              Mileage: 0, // static
              Weight: totalWeight, // static
              LegMiles: 0, // Static
              TotalCharge: 0, // static
              ActualCity: valueToString(row.city),
              ActualState: valueToString(row.state),
              TripMiles: 0, // static
            }))
          : [],
      Taxes: [
        //   {
        //     "BilledAmount": 16.16,
        //     "CurrencyCode": "USD",
        //     "Description": null,
        //     "PkgSequenceKey": 0,
        //     "TaxCode": "GST",
        //     "TaxSequenceKey": 0
        //   }
      ],
      CriteriaId: 1,
      Source: "RateShop", //fixed always
      AuditType: null,
      CustomerKey: data?.customerKey,
      CarrierKey: "-1",
      BranchKey: data?.branch,
      TrailerKey: -1,
      ShipmentDate: data?.shipDate
        ? `/Date(${moment.utc(data?.shipDate).valueOf()})/`
        : null,
      DeliveryDate: `/Date(${moment
        .utc(data?.deliveryDate ? data?.deliveryDate : data?.shipDate)
        .valueOf()})/`,
      PricingType: "PACT", //fixed always
      PricingBasis: "**",
      CurrencyCode: data.currencyCode,
      CargoType: "**",
      WeightUnitOfMeasure: data?.productGridModel?.[0]?.weightUom, // Have to check --------------
      ServiceKey: -1,
      ServiceCode: "**", // fixed
      ServiceDescription: "",
      IntercompanyType: valueToString(data?.shipmentTerms),
      DiscountMode: valueToString(data?.shipmentType),
      ShipmentTerms: valueToString(data?.shipmentTerms),
      TimeFrame: "C", // fixed
      TransportationMode: "**",
      LinerCode: null,
      LinerType: null,
      AccountNumber: null,
      IsShipmentRateshop: "Y", // fixed
      PackageBranchKeys: "-1", //fixed
      MileageMile: 0, // fixed
      MileageKilometer: 0, // fixed
      Weight: valueToNumber(
        sumBy(data?.productGridModel, ({ weight }) => Number(weight))
      ),
      Cube: 0, // fixed
      RatingClass: valueToString(totalClass),
      NumberOfPieces: totalNumberOfPieces,
      Length: valueToNumber(
        sumBy(data?.productGridModel, ({ length }) => Number(length))
      ),
      Width: valueToNumber(
        sumBy(data?.productGridModel, ({ width }) => Number(width))
      ),
      Height: valueToNumber(
        sumBy(data?.productGridModel, ({ height }) => Number(height))
      ),
      ProductCode: null,
      Debug: false,
      TaxableValue: 0,
      DeclaredValue: 0,
      CODValue: 0,
      LDMValue: 0,
      ItemLevelRating: false,
      ContractNumber: null,
      FreightChargeBilled: 0,
      TotalAccessorialBilled: 0,
    },
  };
  return formData;
};

