export const convertRateEnginApiResponseToRateShopModule = (data) => {
  if (data) {
    data = data.map((row) => {
      let tariffRateDate = convertDateToMoment(row.TariffRateDate);
      let pricingEffectiveFrom = convertDateToMoment(row.PricingEffectiveFrom);
      let pricingEffectiveTo = convertDateToMoment(row.PricingEffectiveTo);
      let tariffEffectiveFrom = convertDateToMoment(row.TariffEffectiveFrom);
      let tariffEffectiveTo = convertDateToMoment(row.TariffEffectiveTo);
      return {
        accessorials: row?.Accessorials
          ? row?.Accessorials?.map((accRow) => ({
              auditedChargeAmount: accRow?.AuditedChargeAmount,
              description: accRow?.Description,
              currencyCode: accRow?.CurrencyCode,
              accessorialCode: accRow?.AccessorialCode,
              calculationType: accRow?.CalculationType,
              effectiveFromDate: formateWithMoment(
                accRow.EffectiveFromDate,
                "DD-MMM-YYYY"
              ),
              effectiveToDate: formateWithMoment(
                accRow.EffectiveToDate,
                "DD-MMM-YYYY"
              ),
              fuelChargeIndicator: accRow?.FuelChargeIndicator,
              discountAmount: accRow?.DiscountAmount,
              note: accRow?.ErrorMessage,
            }))
          : [],
        cargoType: row.CargoType,
        carrierKey: row.CarrierKey,
        carrierName: row.CarrierName,
        currencyCode: row.CurrencyCode,
        directPoint: row.DirectPoint,
        error_message: row.ErrorMessage,
        mileage: row.MileageMiles,
        mileageKm: row.MileageKilometers,
        pricingBasis: row.PricingBasis,
        pricingType: row.PricingType,
        serviceDesc: row.ServiceDescription,
        serviceKey: row.ServiceKey,
        serviceType: row.ServiceCode,
        shipmentCost: row.ShipmentCost,
        standardDaysTransit: row.StandardDaysTransit,
        trailerDesc: row.TrailerDescription,
        equipmentTypeDetail: row.TrailerDescription,
        trailerkey: row.TrailerKey,
        convertedCost: row.ConvertedCost,
        freightAccessorialCollection: row.Accessorials,
        convertedCurrencyCode: row.ConvertedCurrency,
        spotQuoteNumber: row.SpotQuoteContractNumber,
        bidComment: row.ErrorMessage,
        effectiveTo: convertDateToMoment(row.PricingEffectiveTo),
        createdOn: moment(new Date()),
        TariffKey: row.TariffKey,
        pricingDetailKey: row.PricingKey,
        accessorialCost: sum(
          row.accessorials
            ? row.accessorials.map(
                ({ AuditedChargeAmount }) => AuditedChargeAmount
              )
            : []
        ),
        rateShopKey: "", //
        rateType: "", //
        flagStandardTranit: "", //
        viewCargoType: null, //
        isRateEntry: null, //
        totalShipmentCost: row.TotalShipmentCost,
        laneId: row.LaneId,
        branchDescription: row.BranchDescription,
        grossCharge: row.GrossCharge,
        totalDiscount: row.TotalDiscount,
        discountPercentage: row.DiscountPercentage,
        ABSTotalCharge: row.ABSTotalCharge,
        totalAccessorialCharge: row.TotalAccessorialCharge,
        totalTaxCharge: row.TotalTaxCharge,
        bureauMinimumUsed: row.BureauMinimumUsed,
        ABSMinimumUsed: row.ABSMinimumUsed,
        ABSMaximumUsed: row.ABSMaximumUsed,
        tariffName: row.TariffName,
        tariffRateDate: tariffRateDate
          ? tariffRateDate?.format("(mm/dd/yyyy)")
          : "",
        pricingEffectiveFrom: pricingEffectiveFrom
          ? pricingEffectiveFrom?.format("(DD-MMM-YYYY)")
          : "",
        pricingEffectiveTo: pricingEffectiveTo
          ? pricingEffectiveTo?.format("(DD-MMM-YYYY)")
          : "",
        tariffEffectiveFrom: tariffEffectiveFrom
          ? tariffEffectiveFrom?.format("(DD-MMM-YYYY)")
          : "",
        tariffEffectiveTo: tariffEffectiveTo
          ? tariffEffectiveTo?.format("(DD-MMM-YYYY)")
          : "",
        accountNumber: row.AccountNumber,
        tariffKey: row.TariffKey,
        lineType: row.AirRailEquipmentType || "N/A",
        liner: row.AirRailOceanLiner || "N/A",
        laneCode: row.LaneCode || "N/A",
        laneKey: row.LaneKey || "N/A",
        items: row.Details
          ? row.Details.map((item) => ({
              ...item,
              numberOfPieces: item.NumberOfPieces,
              billedQuantity: item.BilledQuantity,
              ratingClass: item.RatingClass,
              ABSMinimumUsed: item.ABSMinimumUsed,
              ABSMaximumUsed: item.ABSMaximumUsed,
              rateCostPer: item.RateCostPer,
              discountAmount: item.DiscountAmount,
              billedWeight: item.BilledWeight,
              billedWeightUOM: item.BilledWeightUOM,
              actualWeight: item.ActualWeight,
              pricingBasis: item.PricingBasis,
              rateCharge: item.RateCharge,
              totalCharge: item.TotalCharge,
              actualClass: item.ActualClass,
            }))
          : [],
        taxes: row.Taxes
          ? row.Taxes.map((tax) => ({
              ...tax,
              taxCode: tax.TaxCode,
              description: tax.Description,
              calculationType: tax.CalculationType,
              taxType: tax.TaxType,
              effectiveFromDate: formateWithMoment(
                tax.EffectiveFromDate,
                "DD-MMM-YYYY"
              ),
              effectiveToDate: formateWithMoment(
                tax.EffectiveToDate,
                "DD-MMM-YYYY"
              ),
              auditedChargeAmount: tax.AuditedChargeAmount,
              note: tax.ErrorMessage,
            }))
          : [],
        note: row.ErrorMessage,
        totalDeficitCharge: row.TotalDeficitCharge,
        totalDeficitWeight: row.TotalDeficitWeight,
        deficitWeight: row.DeficitWeight,
        deficitRate: row.DeficitRate,
      };
    });
  }
  data = data.filter(({ totalShipmentCost }) => {
    return Number(totalShipmentCost) > 0;
  });
  data = orderBy(data, ["totalShipmentCost"], ["asc"]);
  return groupBy(data, ({ laneId }) => laneId);
};

export const convertParcelExpressUIToRateAPI = (
  data,
  customerKey,
  isRateRequest
) => {
  const carrierData = isRateRequest
    ? {
        rateFedex: Boolean(data?.carrierForm?.rateFedex),
        fedexAccount: valueToString(data?.carrierForm?.fedexAccount),
        rateUPS: Boolean(data?.carrierForm?.rateUPS),
        upsAccount: valueToString(data?.carrierForm?.upsAccount),
        rateDHL: Boolean(data?.carrierForm?.rateDHL),
        dhlAccount: valueToString(data?.carrierForm?.dhlAccount),
      }
    : {
        LabelFedex: false,
        fedexAccount: "",
        LabelUPS: false,
        upsAccount: "",
        LabelDHL: false,
        dhlAccount: "",
        ShipmentType: valueToString(
          data?.shipmentInformationForm?.shipmentType
        ),
        ShipmentTerms: valueToString(
          data?.shipmentInformationForm?.shipmentTerms
        ),
        ShipperAccountNumber: valueToString(
          data?.shipmentInformationForm?.accountNo
        ),
      };

  if (!isRateRequest) {
    if (data.SelectedRate.carrierType === SP_CARRIERS_TYPES.FEDEX) {
      carrierData.LabelFedex = true;
      carrierData.fedexAccount = valueToString(data?.carrierForm?.fedexAccount);
    } else if (data.SelectedRate.carrierType === SP_CARRIERS_TYPES.UPS) {
      carrierData.LabelUPS = true;
      carrierData.upsAccount = valueToString(data?.carrierForm?.upsAccount);
    } else if (data.SelectedRate.carrierType === SP_CARRIERS_TYPES.DHL) {
      carrierData.LabelDHL = true;
      carrierData.dhlAccount = valueToString(data?.carrierForm?.dhlAccount);
    }
  }

  return {
    parcelPackage: data.packages.map((packageDetails) => ({
      weight: valueToString(packageDetails.weight),
      weightUOM: valueToString(packageDetails.weightUOM),
      packagesQuantity: valueToNumber(packageDetails.noPackages),
      length: valueToString(packageDetails.weight),
      width: valueToString(packageDetails.width),
      height: valueToString(packageDetails.height),
      dimensionsUOM: valueToString(packageDetails.dimensionsUOM),
    })),
    origin: {
      contactName: valueToString(data.locationForm?.ORIGIN_contactName),
      phoneCode: valueToString(data.locationForm?.ORIGIN_phoneCode),
      phoneNumber: valueToString(data.locationForm?.ORIGIN_phoneNumber),
      phoneExtension: valueToString(data.locationForm?.ORIGIN_phoneExtension),
      contactEmail: valueToString(data.locationForm?.ORIGIN_contactEmail),
      companyName: valueToString(data.locationForm?.ORIGIN_companyName),
      streetAddress: valueToString(data.locationForm?.ORIGIN_streetAddress),
      streetAddress2: valueToString(data.locationForm?.ORIGIN_streetAddress2),
      countryCode: valueToString(data.locationForm?.ORIGIN_countryCode),
      postalCode: valueToString(data.locationForm?.ORIGIN_postalCode),
      state: valueToString(data.locationForm?.ORIGIN_state),
      city: valueToString(data.locationForm?.ORIGIN_city),
      deliveryDate: convertDateStringtoMoment(data.locationForm?.shipDate),
    },
    destination: {
      contactName: valueToString(data.locationForm?.DEST_contactName),
      phoneCode: valueToString(data.locationForm?.DEST_phoneCode),
      phoneNumber: valueToString(data.locationForm?.DEST_phoneNumber),
      phoneExtension: valueToString(data.locationForm?.DEST_phoneExtension),
      contactEmail: valueToString(data.locationForm?.DEST_contactEmail),
      companyName: valueToString(data.locationForm?.DEST_companyName),
      streetAddress: valueToString(data.locationForm?.DEST_streetAddress),
      streetAddress2: valueToString(data.locationForm?.DEST_streetAddress2),
      countryCode: valueToString(data.locationForm?.DEST_countryCode),
      postalCode: valueToString(data.locationForm?.DEST_postalCode),
      state: valueToString(data.locationForm?.DEST_state),
      city: valueToString(data.locationForm?.DEST_city),
      deliveryDate: convertDateStringtoMoment(data.locationForm?.shipDate),
    },
    branch: valueToNumber(data?.shipmentInformationForm?.branchKey),
    customerKey: valueToNumber(customerKey),
    ...carrierData,
  };
};

export const freightClassCalculator = (
  productList,
  isHideTheRowDetails = false
) =>
  new Promise(async (resolve, reject) => {
    try {
      await validateProductForCalculateClass(productList, isHideTheRowDetails);
      productList = productList.map((row) => ({
        ...row,
        class: calCulateFreightClass(
          row.length,
          row.width,
          row.height,
          row.weight,
          row.weightUom,
          row.billQty,
          row.billQtyUom,
          row.dimUom,
          row.cube
        ),
      }));
      resolve(productList);
    } catch (error) {
      reject(error);
    }
  });

