export const getShipmentOriginDestinationDetails = (stops) => {
  stops = orderBy(stops, ["displaySequence"], ["asc"]);

  return { origin: stops[0], destination: stops[stops.length - 1] };
};

export const alignStopsByStopNo = (transportationList, index, paramName) => {
  const seqParam = paramName ? paramName : "displaySequence";
  const existIndexNo = findIndex(
    transportationList,
    (row, indexLocal) =>
      Number(row?.[seqParam]) === transportationList?.[index]?.[seqParam] &&
      indexLocal !== index
  );

  if (existIndexNo >= 0) {
    transportationList = arrayMove(transportationList, index, existIndexNo);
    transportationList = transportationList.map((row, index) => ({
      ...row,
      [seqParam]: index + 1,
    }));
    transportationList = orderBy(transportationList, [seqParam], ["asc"]);
  }
  return transportationList;
};

export const generateStopsSequence = (stops, index) => {
  stops = stops.map((row) => ({
    ...row,
    sequence: valueToNumber(row.sequence),
  }));
  if (stops[index].gridOperation == 1) {
    // new stop
    stops[index].sequence =
      max(stops.map(({ sequence }) => (sequence ? sequence : 0))) + 1;
  } else if (stops[index].gridOperation == 2) {
    // modifying stop
    const stopsByType = filter(stops, ["stopType", stops[index].stopType]);
    const stopsBySequence = filter(stopsByType, [
      "sequence",
      stops[index].sequence,
    ]);

    if (stopsBySequence.length > 1) {
      stops[index].sequence =
        max(stopsByType.map(({ sequence }) => (sequence ? sequence : 0))) + 1;
    }
  } else if (stops[index].gridOperation == 3) {
    // deleting stop
  }
  return stops;
};

