import { Font, Page, StyleSheet, Text, View } from "@react-pdf/renderer";
import { stringIsNullOrEmpty } from "../../../../utilz/utilz";
import PDFCheckBoxImg from "./PDFCheckBoxImg";
import opnSansItalic from "../../../../assets/fonts/OpenSans-Italic.ttf";

Font.register({
  family: "Open Sans",
  fonts: [
    {
      src: "https://cdn.jsdelivr.net/npm/open-sans-all@0.1.3/fonts/open-sans-regular.ttf",
    },
    {
      src: "https://cdn.jsdelivr.net/npm/open-sans-all@0.1.3/fonts/open-sans-400.ttf",
      fontWeight: 400,
    },
    {
      src: "https://cdn.jsdelivr.net/npm/open-sans-all@0.1.3/fonts/open-sans-600.ttf",
      fontWeight: 600,
    },
    {
      src: "https://cdn.jsdelivr.net/npm/open-sans-all@0.1.3/fonts/open-sans-700.ttf",
      fontWeight: 700,
    },
    {
      src: "https://cdn.jsdelivr.net/npm/open-sans-all@0.1.3/fonts/open-sans-800.ttf",
      fontWeight: 800,
    },
    { src: opnSansItalic },
  ],
});

Font.register({
  family: "Open Sans Italic",
  fonts: [{ src: opnSansItalic }],
});

Font.registerHyphenationCallback((word) => [word]);

export const PDFRow = ({
  children,
  style = {},
  justify = "start",
  align = "top",
  direction = "row",
  ...rest
}) => {
  const styles = StyleSheet.create({
    row: {
      display: "flex",
      flexDirection: direction,
      justifyContent: justify,
      alignItems: align,
      ...style,
    },
  });
  return (
    <View style={styles.row} {...rest}>
      {children}
    </View>
  );
};

export const PDFText = ({ children, style = {}, ...rest }) => {
  const styles = StyleSheet.create({
    text: {
      fontFamily: "Open Sans",
      fontSize: 10,
      fontWeight: "400",
      wordWrap: "break-word",
      ...style,
    },
  });
  return (
    <Text style={styles.text} {...rest}>
      {children}
    </Text>
  );
};

export const PDFDarkHeader = ({
  children,
  style = {},
  textStyles = {},
  isFirst,
}) => {
  const styles = StyleSheet.create({
    header: {
      width: "100%",
      backgroundColor: "black",
      paddingBottom: isFirst ? 1 : 0,
      paddingVertical: isFirst ? 0 : 1,
      ...style,
    },
  });
  return (
    <Text style={styles.header}>
      <PDFText
        style={{ ...{ color: "white", textAlign: "center" }, ...textStyles }}
      >
        {children}
      </PDFText>
    </Text>
  );
};

export const PDFTable = ({ children, style = {} }) => {
  const styles = StyleSheet.create({
    text: {
      width: "100%",
      overflow: "hidden",
      display: "flex",
      flexDirection: "column",
      ...style,
    },
  });
  return <View style={styles.text}>{children}</View>;
};

export const PDFTableRow = ({ children, style = {}, ...rest }) => {
  const styles = StyleSheet.create({
    text: {
      width: "100%",
      display: "flex",
      flexDirection: "row",
      flexWrap: "nowrap",
      ...style,
      // backgroundColor: "blue",
      // alignItems: "stretch",
    },
  });
  return (
    <View style={styles.text} {...rest}>
      {children}
    </View>
  );
};

export const PDFTableCell = ({
  children,
  style = {},
  width = "auto",
  borderTop = true,
  borderRight = true,
  borderBottom = true,
  borderLeft = true,
  padding = 3,
  justify = "center",
  ...rest
}) => {
  const styles = StyleSheet.create({
    view: {
      marginTop: -1,
      marginLeft: -1,
      width,
      paddingHorizontal: padding,
      paddingVertical: 0,
      borderTop: getTableCellBorder(borderTop),
      borderRight: getTableCellBorder(borderRight),
      borderBottom: getTableCellBorder(borderBottom),
      borderLeft: getTableCellBorder(borderLeft),
      ...style,
      // backgroundColor: "yellow",
      display: "flex",
      flexDirection: "column",
      justifyContent: justify,
    },
  });
  return (
    <View style={styles.view} {...rest}>
      {children}
    </View>
  );
};

export const PDFDivider = ({ style = {} }) => {
  const styles = StyleSheet.create({
    view: {
      width: "100%",
      borderBottom: "1px solid black",
      ...style,
    },
  });
  return <View style={styles.view} />;
};

export const PdfGetTextUnderline = ({ value, fontWeight = "400" }) => {
  return (
    <PDFText
      style={{
        textDecoration: value ? "underline" : "none",
        fontWeight,
      }}
    >
      {stringIsNullOrEmpty(value) ? "__________________" : value}
    </PDFText>
  );
};

export const PDFCheckBox = ({
  value = false,
  label = "label",
  size = 10,
  fontSize = 8,
  fontWeight = "700",
  isReverse = false,
  colon = true,
}) => {
  const styles = StyleSheet.create({
    text: {
      fontSize,
      fontWeight,
    },
  });
  return (
    <PDFRow
      direction={isReverse ? "row-reverse" : "row"}
      style={{ flexWrap: "nowrap", alignItems: "center" }}
      justify={`flex-${isReverse ? "end" : "start"}`}
    >
      <PDFText style={styles.text}>{label}</PDFText>
      {colon && (
        <PDFText style={{ ...styles.text, marginLeft: 1, marginRight: 3 }}>
          :
        </PDFText>
      )}

      {colon ? "" : <View style={{ width: 5 }}></View>}

      <View>
        <PDFCheckBoxImg size={size} isChecked={value} />
      </View>
    </PDFRow>
  );
};

export const PDFPage = ({
  children,
  paddingHorizontal = 10,
  paddingVertical = 20,
  style = {},
}) => {
  const styles = StyleSheet.create({
    page: {
      ontFamily: "Open Sans",
      flexDirection: "column",
      display: "flex",
      height: "auto",
      paddingHorizontal,
      paddingVertical,
    },
  });

  return (
    <Page
      size={"A4"}
      style={{ ...styles.page, ...style }}
      orientation={"portrait"}
    >
      {children}
    </Page>
  );
};

