import moment from "moment";
import { DEFAULT_DATE_FORMAT } from "../contants/common";
import { numberPadding } from "./utilz";

const convertUtcToLocal = (date) => new Date(`${date}Z`).toString();

export const getLabelFromDate = (date) => {
  return date ? moment(convertUtcToLocal(date)).format("MMM DD, YYYY") : "";
};

export const getLabelFromDateTime = (date, is12HoursFormat = true) => {
  return date
    ? moment(convertUtcToLocal(date)).format(
        `MMM DD YYYY | ${is12HoursFormat ? "hh:mm a" : "HH:mm"}`
      )
    : "";
};

export const getDateLabelFormat = (date, is12HoursFormat = true) => {
  return date
    ? moment(date).format(
        `MMM DD YYYY | ${is12HoursFormat ? "hh:mm a" : "HH:mm"}`
      )
    : "";
};

export const convertBidExpiryDate = (date) =>
  moment(date, "MM/DD/YYYY h:mm A", "America/New_York");

export const getDateLabel = (date) => {
  return date ? moment(date).format(DEFAULT_DATE_FORMAT) : "";
};

export const getPODateOrder = (date) => {
  return date ? moment(date).format(DEFAULT_DATE_FORMAT) : "";
};

export const getPODateLabel = (date) => {
  return date ? moment(date).format(DEFAULT_DATE_FORMAT) : "";
};

export const getTimeLabel = (date) => {
  return date ? moment(date).format("hh:mm:ss A") : "";
};

export const convertTimetoMoment = (time) =>
  time ? moment(time, "hh:mm A") : undefined;

export const formateWithMoment = (
  date,
  format = "MM/DD/YYYY",
  dateStringFormat = false
) => (date ? moment(date, dateStringFormat).format(format) : "");

export const convertDateStringtoMoment = (time) =>
  time ? moment(time, "YYYY-M-DThh:mm:ss") : null;

export const convertDateToMoment = (date, format = "MM/DD/YYYY") =>
  date ? moment(date, format) : null;

export const timerCounter = (timerData) => {
  let timerInterval = false;
  try {
    if (timerData) {
      const expectedBidTime = convertBidExpiryDate(timerData);

      if (expectedBidTime) {
        timerInterval = setInterval(() => {
          var duration = moment.duration(
            moment(expectedBidTime).diff(
              moment.tz("America/New_York").format("MM/DD/YYYY hh:mm:ss A")
            )
          );
          if (duration._milliseconds > 0) {
            var hours = parseInt(duration.asHours());
            var minutes = parseInt(duration.asMinutes()) % 60;
            var seconds = parseInt(duration.asSeconds()) % 60;
            // setBidExpiry(
            //   `${numberPadding(hours)}:${numberPadding(
            //     minutes
            //   )}:${numberPadding(seconds)}`
            // );
          } else {
            if (timerInterval) {
              clearInterval(timerInterval);
            }
            // setBidExpiry("00:00:00");
          }
        }, 1000);
      }
    }
  } catch (error) {}
};

export const dateTimeToSingleValue = (date, time) => {
  if (date) {
    date = date.format("YYYY-MM-DD");
  }
  if (time) {
    time = time.format("HH:mm:ss.SSS");
  } else {
    time = "00:00:00.000";
  }

  if (!time || !date) {
    return null;
  }
  return `${date}T${time}Z`;
};

export const combineDateTimeMoment = (date = false, time = false) => {
  if (date && time) {
    var dateString = date?.format("DD/MM/YYYY");
    var timeString = time?.format("HH:mm:ss A");
    return moment(dateString + " " + timeString, "DD/MM/YYYY HH:mm:ss A");
  } else {
    return null;
  }
};

export const getFormattedTimeString = (date = new Date()) =>
  `${String(moment(date).format()).split("+")[0]}Z`;
