import { pdf } from "@react-pdf/renderer";
import PDF from "./PDF";

const generatePLPDF = async (props) => {
  console.log("started PL");
  const data = JSON.parse(props);
  console.log("props", data);
  const result = await pdf(<PDF plData={data} />).toBlob();
  console.log("ended PL");
  return result;
};

export default generatePLPDF;
