import React from "react";
import CMRPrintDocument from "../../CMRPrintDocument";
import { Document } from "@react-pdf/renderer";

const PDF = ({
  shipmentDetails,
  inputData,
  productList,
  documentList,
  carrierData,
  productInputs,
}) => {
  return (
    <Document>
      <CMRPrintDocument
        shipmentDetails={shipmentDetails}
        inputData={inputData}
        productList={productList}
        documentList={documentList}
        carrierData={carrierData}
        productInputs={productInputs}
      />
    </Document>
  );
};

export default PDF;
