import React, { useMemo } from "react";
import { StyleSheet, View } from "@react-pdf/renderer";
import {
  PDFDarkHeader,
  PDFPage,
  PDFRow,
  pdfStyles,
  PDFTable,
  PDFTableCell,
  PDFTableRow,
  PDFText,
} from "../../components/CommonPdfComponents";
import { getShipmentOriginDestinationDetails } from "../../../../../utilz/shipmentDataManupulation";
import { convertToFloat } from "../../../../../utilz/utilz";
import { formateWithMoment } from "../../../../../utilz/dates";

const ImoPrintDocument = ({ shipmentDetails, inputData, productList }) => {
  const { origin, destination } = useMemo(
    () =>
      getShipmentOriginDestinationDetails(shipmentDetails?.transportationList),
    [shipmentDetails?.transportationList]
  );
  return (
    <PDFPage>
      <View style={{ ...pdfStyles.border, borderTop: "none" }}>
        <View
          fixed
          style={{
            ...styles.header,
            ...pdfStyles.borderBottom,
            ...pdfStyles.borderTop,
          }}
        >
          <PDFRow style={styles.headerRow}>
            <View></View>
            <View>
              <PDFText style={styles.title}>
                IMO DANGEROUS GOODS DECLARATION
              </PDFText>
            </View>
            <View>
              <PDFText
                style={{ fontSize: 10, fontWeight: "700" }}
                fixed
                render={(data) => {
                  return `Page ${data.pageNumber} of ${data.totalPages}`;
                }}
              />
            </View>
          </PDFRow>
        </View>
        <View style={pdfStyles.borderBottom}>
          <PDFRow>
            <View
              style={{
                ...{ height: "100%", width: "50%" },
                ...pdfStyles.borderRight,
              }}
            >
              <PDFDarkHeader isFirst={true}>Shipper</PDFDarkHeader>
              <View style={styles.section}>
                <PDFText>{origin?.name}</PDFText>
                <PDFText>{origin?.address1}</PDFText>
                <PDFText>
                  {origin?.city}, {origin?.stateCode} {origin?.zipCode}{" "}
                  {origin?.countryName}
                </PDFText>
              </View>
            </View>
            <View
              style={{
                ...{ height: "100%", width: "50%" },
              }}
            >
              <PDFRow direction={"column"} justify="space-between">
                <View style={{ ...styles.section, ...pdfStyles.borderBottom }}>
                  <PDFText>Transportation Document Number</PDFText>
                  <PDFText>{shipmentDetails?.shipmentNo}</PDFText>
                </View>
                <View style={styles.section}>
                  <PDFText>Shipper's Reference</PDFText>
                  <PDFText>{inputData?.shippersRef}</PDFText>
                </View>
              </PDFRow>
            </View>
          </PDFRow>
        </View>
        <View style={pdfStyles.borderBottom}>
          <PDFRow>
            <View
              style={{
                ...{ height: "100%", width: "50%" },
                ...pdfStyles.borderRight,
              }}
            >
              <PDFDarkHeader isFirst={true}>Consignee</PDFDarkHeader>
              <View style={styles.section}>
                <PDFText>{destination?.name}</PDFText>
                <PDFText>{destination?.address1}</PDFText>
                <PDFText>
                  {destination?.city}, {destination?.stateCode}{" "}
                  {destination?.zipCode} {destination?.countryName}
                </PDFText>
              </View>
            </View>
            <View
              style={{
                ...{ height: "100%", width: "50%" },
              }}
            >
              <PDFRow direction={"column"} justify="space-between">
                <View style={{ ...styles.section, ...pdfStyles.borderBottom }}>
                  <PDFText>Freight Forwarder's Reference</PDFText>
                  <PDFText>{shipmentDetails?.freightForwardRef}</PDFText>
                </View>
                <View style={styles.section}>
                  <PDFText> Carrier (to be declared by the Carrier)</PDFText>
                  <PDFText>{inputData?.carrierDeclare}</PDFText>
                </View>
              </PDFRow>
            </View>
          </PDFRow>
        </View>
        <View style={pdfStyles.borderBottom}>
          <PDFDarkHeader isFirst={true}>SHIPPER'S DECLARATION</PDFDarkHeader>
          <View style={styles.section}>
            <PDFText style={{ fontSize: 8 }}>
              I hereby declare that the contents of this consignment are fully
              and accurately described above by the proper shipping name(s), and
              are classified, packaged, marked and labeled/placarded, and are in
              all respects in proper condition for transport according to
              applicable international and national governmental regulations.
            </PDFText>
            <PDFText>{inputData?.shipperDeclaration}</PDFText>
          </View>
        </View>
        <View style={pdfStyles.borderBottom}>
          <PDFRow>
            <View
              style={{
                ...{ height: "100%", width: "50%" },
                ...pdfStyles.borderRight,
              }}
            >
              <PDFRow direction={"column"} justify="space-between">
                <View style={{ ...styles.section, ...pdfStyles.borderBottom }}>
                  <PDFText>Vessel/Flight and Date</PDFText>
                  <PDFText>
                    {inputData?.vesselFlight}
                    {"     "}
                    {formateWithMoment(inputData?.vesselDate)}
                  </PDFText>
                </View>
                <View style={styles.section}>
                  <PDFText>Port/Place Handling</PDFText>
                  <PDFText>{inputData?.portHandling}</PDFText>
                </View>
              </PDFRow>
            </View>
            <View
              style={{
                ...{ height: "100%", width: "50%" },
              }}
            >
              <View style={styles.section}>
                <PDFText style={{ marginBottom: 5 }}>
                  Additional Handling Information
                </PDFText>
                <PDFText>{inputData?.additionalHandlingInfo}</PDFText>
              </View>
            </View>
          </PDFRow>
        </View>
        <View>
          <PDFTable>
            <PDFTableRow>
              <PDFTableCell width="20%">
                <PDFText style={styles.tableHeader}>Shipping Marks</PDFText>
              </PDFTableCell>
              <PDFTableCell width="60%">
                <PDFText style={styles.tableHeader}>
                  Number and Kind of Packages, Description of Goods
                </PDFText>
              </PDFTableCell>
              <PDFTableCell width="13%">
                <PDFText style={styles.tableHeader}>GW (kg)</PDFText>
              </PDFTableCell>
              <PDFTableCell width="13%" borderRight={false}>
                <PDFText style={styles.tableHeader}>CUBE (m3)</PDFText>
              </PDFTableCell>
            </PDFTableRow>
            {productList.map((product) => (
              <PDFTableRow key={product.productCode}>
                <PDFTableCell width="20%">
                  <PDFText style={styles.tableCol}>
                    {product?.productCode}
                  </PDFText>
                </PDFTableCell>
                <PDFTableCell width="60%">
                  <PDFText style={styles.tableCol}>
                    {product?.productDescription}, ({product?.billQty}{" "}
                    {product?.billQtyUomName})
                  </PDFText>
                </PDFTableCell>
                <PDFTableCell width="13%">
                  <PDFText style={styles.tableCol}>
                    {convertToFloat(
                      String(product?.weightUom).trim().toUpperCase() === "LB"
                        ? product?.weight * 0.45359237
                        : product?.weight
                    )}
                  </PDFText>
                </PDFTableCell>
                <PDFTableCell width="13%" borderRight={false}>
                  <PDFText style={styles.tableCol}>{product?.cube}</PDFText>
                </PDFTableCell>
              </PDFTableRow>
            ))}
          </PDFTable>
        </View>
        <View style={pdfStyles.borderBottom}>
          <PDFRow>
            <View
              style={{
                width: "15%",
                ...styles.section,
                ...pdfStyles.borderRight,
              }}
            >
              <PDFText>CTU ID No.</PDFText>
              <PDFText>{inputData?.ctuIdNo}</PDFText>
            </View>
            <View
              style={{
                width: "15%",
                ...styles.section,
                ...pdfStyles.borderRight,
              }}
            >
              <PDFText>Seal No.</PDFText>
              <PDFText>{inputData?.sealNo}</PDFText>
            </View>
            <View
              style={{
                width: "22%",
                ...styles.section,
                ...pdfStyles.borderRight,
              }}
            >
              <PDFText>CTU Size and Type</PDFText>
              <PDFText>{inputData?.ctuSizeAndType}</PDFText>
            </View>
            <View
              style={{
                width: "22%",
                ...styles.section,
                ...pdfStyles.borderRight,
              }}
            >
              <PDFText>Tare Mass (kg)</PDFText>
              <PDFText>{inputData?.totalWeight}</PDFText>
            </View>
            <View
              style={{
                width: "22%",
                ...styles.section,
              }}
            >
              <PDFText>Total Gross Mass (kg)</PDFText>
              <PDFText>{inputData?.shipmentTotalWeight}</PDFText>
            </View>
          </PDFRow>
        </View>
        <View style={pdfStyles.borderBottom}>
          <PDFRow>
            <View
              style={{
                width: "34%",
                ...styles.section,
                ...pdfStyles.borderRight,
              }}
            >
              <PDFText style={{ marginBottom: 5 }}>
                Container/Vehicle packing Certificate
              </PDFText>
              <PDFText style={{ fontSize: 8 }}>
                I hereby declare that the goods described above have been
                packed/loaded into the container/vehicle identified above in
                accordance with the applicable provisions
              </PDFText>
            </View>
            <View
              style={{
                width: "68%",
                ...styles.section,
              }}
            >
              <PDFText style={{ marginBottom: 5 }}>
                Receiving Organization Receipt
              </PDFText>
              <PDFText style={{ fontSize: 8 }}>
                Received the above number of packages/containers/trailers in
                apparent good order and condition, unless stated hereon:
              </PDFText>
              <PDFText style={{ fontWeight: 700 }}>
                RECEIVING ORGANIZATION REMARKS:
              </PDFText>
              <PDFText>{inputData?.organizationRemarks}</PDFText>
            </View>
          </PDFRow>
        </View>
        <View style={pdfStyles.borderBottom}>
          <PDFRow>
            <View
              style={{
                width: "34%",
                ...styles.section,
                ...pdfStyles.borderRight,
              }}
            >
              <PDFText>Name of Company:</PDFText>
              <PDFText>{inputData?.companyName}</PDFText>
            </View>
            <View
              style={{
                width: "34%",
                ...styles.section,
                ...pdfStyles.borderRight,
              }}
            >
              <PDFText>Hauler's Name:</PDFText>
              <PDFText>{inputData?.haulersName}</PDFText>
            </View>
            <View
              style={{
                width: "34%",
                ...styles.section,
              }}
            >
              <PDFText>Name of Company Preparing Note:</PDFText>
              <PDFText>{inputData?.pnCompanyName}</PDFText>
            </View>
          </PDFRow>
        </View>
        <View style={pdfStyles.borderBottom}>
          <PDFRow>
            <View
              style={{
                width: "34%",
                ...styles.section,
                ...pdfStyles.borderRight,
              }}
            >
              <PDFText>Name/Status of Declarant:</PDFText>
              <PDFText>{inputData?.nameStatusDeclarant}</PDFText>
            </View>
            <View
              style={{
                width: "34%",
                ...styles.section,
                ...pdfStyles.borderRight,
              }}
            >
              <PDFText>Vehicle Registration No.:</PDFText>
              <PDFText>{inputData?.vehicleRegNo}</PDFText>
            </View>
            <View
              style={{
                width: "34%",
                ...styles.section,
              }}
            >
              <PDFText>Name/Status of Declarant:</PDFText>
              <PDFText>{inputData?.pnNameStatusDeclarant}</PDFText>
            </View>
          </PDFRow>
        </View>
        <View style={pdfStyles.borderBottom}>
          <PDFRow>
            <View
              style={{
                width: "34%",
                ...styles.section,
                ...pdfStyles.borderRight,
              }}
            >
              <PDFText>Place and Date:</PDFText>
              <PDFText>
                {inputData?.place}
                {"     "}
                {formateWithMoment(inputData?.date)}
              </PDFText>
            </View>
            <View
              style={{
                width: "34%",
                ...styles.section,
                ...pdfStyles.borderRight,
              }}
            >
              <PDFText>Driver Name and Date.:</PDFText>
              <PDFText>
                {inputData?.driverName}
                {"     "}
                {formateWithMoment(inputData?.driverDate)}
              </PDFText>
            </View>
            <View
              style={{
                width: "34%",
                ...styles.section,
              }}
            >
              <PDFText>Place and Date:</PDFText>
              <PDFText>
                {inputData?.pnPlace}
                {"     "}
                {formateWithMoment(inputData?.pnDate)}
              </PDFText>
            </View>
          </PDFRow>
        </View>
        <View>
          <PDFRow>
            <View
              style={{
                width: "34%",
                ...styles.section,
                ...pdfStyles.borderRight,
              }}
            >
              <PDFText>Signature of Declarant:</PDFText>
              <PDFText>{inputData?.signature}</PDFText>
            </View>
            <View
              style={{
                width: "34%",
                ...styles.section,
                ...pdfStyles.borderRight,
              }}
            >
              <PDFText>Driver's Signature:</PDFText>
              <PDFText>{inputData?.driverSignature}</PDFText>
            </View>
            <View
              style={{
                width: "34%",
                ...styles.section,
              }}
            >
              <PDFText>Signature of Declarant:</PDFText>
              <PDFText>{inputData?.pnSignature}</PDFText>
            </View>
          </PDFRow>
        </View>
      </View>
    </PDFPage>
  );
};

const styles = StyleSheet.create({
  header: { paddingHorizontal: 10, paddingVertical: 5 },
  title: { fontSize: 16, fontWeight: "700" },
  headerRow: {
    justifyContent: "space-between",
    alignItems: "center",
  },
  section: {
    paddingHorizontal: 5,
    paddingVertical: 10,
  },
  imageSection: {
    width: "100%",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    paddingVertical: 10,
  },
  image: {
    width: "60%",
  },
  tableHeader: {
    fontWeight: "700",
    fontSize: 8,
    textAlign: "center",
  },
  tableCol: {
    fontSize: 8,
  },
});

export default ImoPrintDocument;
