import React, { Fragment, useMemo } from "react";
import { StyleSheet, View } from "@react-pdf/renderer";
import {
  PDFPage,
  PDFRow,
  pdfStyles,
  PDFTable,
  PDFTableCell,
  PDFTableRow,
  PDFText,
} from "../../components/CommonPdfComponents";
import { getShipmentOriginDestinationDetails } from "../../../../../utilz/shipmentDataManupulation";
import { convertToFloat } from "../../../../../utilz/utilz";
import { formateWithMoment } from "../../../../../utilz/dates";
import { sumBy } from "lodash";

const CMRPrintDocument = ({
  shipmentDetails,
  inputData,
  productList = [],
  productInputs,
  documentList = [],
  carrierData,
}) => {
  const { origin, destination } = useMemo(
    () =>
      getShipmentOriginDestinationDetails(shipmentDetails?.transportationList),
    [shipmentDetails?.transportationList]
  );
  return (
    <PDFPage
      style={{
        position: "relative",
      }}
      paddingHorizontal={0}
      paddingVertical={1}
    >
      <View>
        <View
          style={{
            ...pdfStyles.border,
            marginLeft: 10,
            marginRight: 1,
          }}
        >
          <PDFRow style={{ ...styles.headerRow, ...pdfStyles.borderBottom }}>
            <View></View>
            <View>
              <PDFText style={styles.title}>CMR</PDFText>
            </View>
            <View></View>
          </PDFRow>
          <PDFRow style={{ ...pdfStyles.borderBottom }}>
            <View
              style={{
                flex: 1,
                ...pdfStyles.borderRight,
                ...styles.cellPadding,
              }}
            >
              <PDFText style={{ ...styles.tableCol, fontWeight: 800 }}>
                1 Sender (Name, Address, Country)
              </PDFText>
              <View style={{ marginVertical: 2 }}>
                <PDFText style={styles.tableCol}>{origin?.name}</PDFText>
                <PDFText style={styles.tableCol}>{origin?.address1}</PDFText>
                <PDFText style={styles.tableCol}>
                  {origin?.city}, {origin?.stateCode} {origin?.zipCode}{" "}
                  {origin?.countryCode}
                </PDFText>
              </View>
              <View style={{ marginVertical: 2 }}>
                <PDFText style={styles.tableCol}>VET auth. No:</PDFText>
              </View>
            </View>
            <View style={{ flex: 1, ...styles.cellPadding }}>
              <PDFText style={{ ...styles.tableCol, fontWeight: 800 }}>
                INTERNATIONAL CONSIGNMENT NOTE
              </PDFText>
              <View style={{ marginVertical: 2 }}>
                <PDFText style={styles.tableCol}>
                  This carriage is subject, notwithstanding any clause to the
                  contract for the Convention on the contract for the
                  International Carriage of goods by road (CMR).
                </PDFText>
              </View>
            </View>
          </PDFRow>
          <PDFRow style={{ ...pdfStyles.borderBottom }}>
            <View
              style={{
                flex: 1,
                ...pdfStyles.borderRight,
                ...styles.cellPadding,
              }}
            >
              <PDFText style={{ ...styles.tableCol, fontWeight: 800 }}>
                2 Consignee (Name, Address, Country)
              </PDFText>
              <View style={{ marginVertical: 2 }}>
                <PDFText style={styles.tableCol}>{destination?.name}</PDFText>
                <PDFText style={styles.tableCol}>
                  {destination?.address1}
                </PDFText>
                <PDFText style={styles.tableCol}>
                  {destination?.city}, {destination?.stateCode}{" "}
                  {destination?.zipCode} {destination?.countryCode}
                </PDFText>
              </View>
            </View>
            <View style={{ flex: 1, ...styles.cellPadding }}>
              <PDFText style={{ ...styles.tableCol, fontWeight: 800 }}>
                16 Carrier (Name, Address, Country)
              </PDFText>
              <View style={{ marginVertical: 2 }}>
                <PDFText style={styles.tableCol}>
                  {carrierData?.carrierName}
                </PDFText>
                <PDFText style={styles.tableCol}>
                  {carrierData?.carrierAddress1}
                </PDFText>
                <PDFText style={styles.tableCol}>
                  {carrierData?.carrierCity}, {carrierData?.carrierStateCode}{" "}
                  {carrierData?.carrierZip} {carrierData?.carrierCountryName}
                </PDFText>
              </View>
            </View>
          </PDFRow>
          <PDFRow style={{ ...pdfStyles.borderBottom }}>
            <View
              style={{
                flex: 1,
                ...pdfStyles.borderRight,
                ...styles.cellPadding,
              }}
            >
              <PDFText style={{ ...styles.tableCol, fontWeight: 800 }}>
                3 Place of delivery of goods
              </PDFText>
              <View style={{ marginVertical: 2 }}>
                <PDFText style={styles.tableCol}>{destination?.name}</PDFText>
                <PDFText style={styles.tableCol}>
                  {destination?.address1}
                </PDFText>
                <PDFText style={styles.tableCol}>
                  {destination?.city}, {destination?.stateCode}{" "}
                  {destination?.zipCode} {destination?.countryCode}
                </PDFText>
              </View>
            </View>
            <View style={{ flex: 1, ...styles.cellPadding }}>
              <PDFText style={{ ...styles.tableCol, fontWeight: 800 }}>
                17 Successive carries (Name, Address, Country)
              </PDFText>
              <View style={{ marginVertical: 2 }}>
                <PDFText style={styles.tableCol}>
                  {inputData?.successiveCarriers}
                </PDFText>
              </View>
            </View>
          </PDFRow>
          <PDFRow style={{ ...pdfStyles.borderBottom }}>
            <View
              style={{
                flex: 1,
                ...pdfStyles.borderRight,
                ...styles.cellPadding,
              }}
            >
              <PDFText style={{ ...styles.tableCol, fontWeight: 800 }}>
                4 Place and date of taking over the goods
              </PDFText>
              <View style={{ marginVertical: 2 }}>
                <PDFText style={styles.tableCol}>{origin?.name}</PDFText>
                <PDFText style={styles.tableCol}>{origin?.address1}</PDFText>
                <PDFText style={styles.tableCol}>
                  {origin?.city}, {origin?.stateCode} {origin?.zipCode}{" "}
                  {origin?.countryCode}
                </PDFText>
                <View style={{ marginVertical: 2 }}>
                  <PDFText style={styles.tableCol}>
                    Date:
                    {formateWithMoment(origin?.requestedDate, "MM-DD-yyyy")}
                  </PDFText>
                </View>
              </View>
            </View>
            <View style={{ flex: 1, ...styles.cellPadding }}>
              <PDFText style={{ ...styles.tableCol, fontWeight: 800 }}>
                18 Carriers reservations and observations
              </PDFText>
              <View style={{ marginVertical: 2 }}>
                <PDFText style={styles.tableCol}>
                  {inputData?.carriersReservations}
                </PDFText>
              </View>
            </View>
          </PDFRow>
          <View style={{ ...styles.cellPadding }}>
            <PDFText style={{ ...styles.tableCol, fontWeight: 800 }}>
              5 Annexed documents
            </PDFText>
            <View>
              {documentList.map((file, key) => (
                <PDFRow key={key} style={{ marginBottom: 2 }}>
                  <View style={{ width: "5%" }}>
                    <PDFText style={styles.tableCol}>{key + 1}</PDFText>
                  </View>
                  <View style={{ width: "20%" }}>
                    <PDFText style={styles.tableCol}>
                      {file.fileDescription}
                    </PDFText>
                  </View>
                  <View style={{ width: "30%" }}>
                    <PDFText style={styles.tableCol}>
                      {file.referenceNo}
                    </PDFText>
                  </View>
                  <View style={{}}>
                    <PDFText style={styles.tableCol}>
                      {file.carrierName}
                    </PDFText>
                  </View>
                </PDFRow>
              ))}
            </View>
          </View>
          <PDFTable style={{ ...pdfStyles.borderTop }}>
            <PDFTableRow>
              <PDFTableCell width="12%">
                <PDFText style={styles.tableHeader}>
                  6 Marks and Numbers
                </PDFText>
              </PDFTableCell>
              <PDFTableCell width="12%">
                <PDFText style={styles.tableHeader}>
                  7 Number of packages
                </PDFText>
              </PDFTableCell>
              <PDFTableCell width="12%">
                <PDFText style={styles.tableHeader}>
                  8 Method of packing
                </PDFText>
              </PDFTableCell>
              <PDFTableCell width="30%">
                <PDFText style={styles.tableHeader}>
                  9 Nature of the goods
                </PDFText>
              </PDFTableCell>
              <PDFTableCell width="12%">
                <PDFText style={styles.tableHeader}>
                  10 Statistica l number
                </PDFText>
              </PDFTableCell>
              <PDFTableCell width="12%">
                <PDFText style={styles.tableHeader}>
                  11 Gross weight in kg
                </PDFText>
              </PDFTableCell>
              <PDFTableCell borderRight={false} width="12%">
                <PDFText style={styles.tableHeader}>12 Volume in m3</PDFText>
              </PDFTableCell>
            </PDFTableRow>
            {productList.map((product, key) => (
              <PDFTableRow key={key}>
                <PDFTableCell width="12%">
                  <PDFText style={styles.tableCol}>
                    {productInputs?.[key]?.marksAndNumbers}
                  </PDFText>
                </PDFTableCell>
                <PDFTableCell width="12%">
                  <PDFText style={styles.tableCol}>{product?.billQty}</PDFText>
                </PDFTableCell>
                <PDFTableCell width="12%">
                  <PDFText style={styles.tableCol}>
                    {product?.billQtyUomName}
                  </PDFText>
                </PDFTableCell>
                <PDFTableCell width="30%">
                  <PDFText style={styles.tableCol}>
                    {product?.productDescription}
                  </PDFText>
                </PDFTableCell>
                <PDFTableCell width="12%">
                  <PDFText style={styles.tableCol}>
                    {productInputs?.[key]?.statisticalNumber}
                  </PDFText>
                </PDFTableCell>
                <PDFTableCell width="12%">
                  <PDFText style={styles.tableCol}>
                    {convertToFloat(
                      String(product?.weightUom).trim().toUpperCase() === "LB"
                        ? product?.weight * 0.45359237
                        : product?.weight
                    )}
                  </PDFText>
                </PDFTableCell>
                <PDFTableCell borderRight={false} width="12%">
                  {/* <PDFText style={styles.tableCol}>{product?.cube}</PDFText> */}
                </PDFTableCell>
              </PDFTableRow>
            ))}
          </PDFTable>
          <View style={{ ...pdfStyles.borderBottom, ...styles.cellPadding }}>
            <PDFText style={{ ...styles.tableCol, fontWeight: 800 }}>
              Delivery Note
            </PDFText>
            <View style={{ marginBottom: 2 }}>
              <PDFText style={styles.tableCol}>
                Total gross weight :{" "}
                {convertToFloat(
                  String(shipmentDetails?.weightUOM).trim().toUpperCase() ===
                    "LB"
                    ? shipmentDetails?.shipmentTotalWeight * 0.45359237
                    : shipmentDetails?.shipmentTotalWeight
                )}{" "}
                kg
              </PDFText>
              <PDFText style={styles.tableCol}>
                Total packages:{" "}
                {sumBy(productList, ({ billQty }) => Number(billQty) || 0)}
              </PDFText>
              <PDFText style={styles.tableCol}>
                Shipping Number: {shipmentDetails?.proNo}
              </PDFText>
            </View>
          </View>
          <PDFRow>
            <View
              style={{
                flex: 1,
                ...pdfStyles.borderRight,
                ...pdfStyles.borderBottom,
              }}
            >
              <View
                style={{ ...pdfStyles.borderBottom, ...styles.cellPadding }}
              >
                <PDFText style={styles.tableHeader}>
                  13 Senders instructions (Customs and other formalities)
                </PDFText>
              </View>
              <View style={{ ...styles.cellPadding }}>
                <PDFText style={styles.tableCol}>
                  {inputData?.sendersInstructions}
                </PDFText>
              </View>
            </View>
            <View style={{ flex: 1 }}>
              <PDFTable>
                <PDFTableRow>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableHeader}>
                      19 To be paid by:
                    </PDFText>
                  </PDFTableCell>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableHeader}>Sender</PDFText>
                  </PDFTableCell>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableHeader}>Currency</PDFText>
                  </PDFTableCell>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableHeader}>Consignee</PDFText>
                  </PDFTableCell>
                </PDFTableRow>
                <PDFTableRow>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableHeader}>
                      Carrier charges
                    </PDFText>
                  </PDFTableCell>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableCol}>
                      {inputData?.carrierChargesSender}
                    </PDFText>
                  </PDFTableCell>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableCol}>
                      {inputData?.carrierChargesCurrency}
                    </PDFText>
                  </PDFTableCell>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableCol}>
                      {inputData?.carrierChargesConsignee}
                    </PDFText>
                  </PDFTableCell>
                </PDFTableRow>
                <PDFTableRow>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableHeader}>
                      Supplement charges
                    </PDFText>
                  </PDFTableCell>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableCol}>
                      {inputData?.supplementChargesSender}
                    </PDFText>
                  </PDFTableCell>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableCol}>
                      {inputData?.supplementChargesCurrency}
                    </PDFText>
                  </PDFTableCell>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableCol}>
                      {inputData?.supplementChargesConsignee}
                    </PDFText>
                  </PDFTableCell>
                </PDFTableRow>
                <PDFTableRow>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableHeader}>Other charges</PDFText>
                  </PDFTableCell>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableCol}>
                      {inputData?.otherChargesSender}
                    </PDFText>
                  </PDFTableCell>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableCol}>
                      {inputData?.otherChargesCurrency}
                    </PDFText>
                  </PDFTableCell>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableCol}>
                      {inputData?.otherChargesConsignee}
                    </PDFText>
                  </PDFTableCell>
                </PDFTableRow>
                <PDFTableRow>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableHeader}>
                      Total to be paid
                    </PDFText>
                  </PDFTableCell>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableCol}>
                      {inputData?.totalChargesSender}
                    </PDFText>
                  </PDFTableCell>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableCol}>
                      {inputData?.totalChargesCurrency}
                    </PDFText>
                  </PDFTableCell>
                  <PDFTableCell width="25%">
                    <PDFText style={styles.tableCol}>
                      {inputData?.totalChargesConsignee}
                    </PDFText>
                  </PDFTableCell>
                </PDFTableRow>
              </PDFTable>
            </View>
          </PDFRow>
          <View style={{ ...pdfStyles.borderBottom, ...styles.cellPadding }}>
            <PDFText style={styles.tableHeader}>
              14 Cash on delivery :{" "}
              <PDFText style={styles.tableCol}>
                {inputData?.cashOnDelivery}
              </PDFText>
            </PDFText>
          </View>
          <PDFRow style={pdfStyles.borderBottom}>
            <View
              style={{
                ...styles.tableCol,
                flex: 1,
                ...pdfStyles.borderRight,
                ...styles.cellPadding,
              }}
            >
              <PDFText style={styles.tableHeader}>
                15 Directions as to payment for carriage
              </PDFText>
              <PDFText style={{ ...styles.tableCol, marginVertical: 3 }}>
                {inputData?.directionPayment}
              </PDFText>
            </View>
            <View
              style={{
                ...styles.tableCol,
                flex: 1,
                ...styles.cellPadding,
              }}
            >
              <PDFText style={styles.tableHeader}>
                20 Special agreements
              </PDFText>
              <PDFText style={{ ...styles.tableCol, marginVertical: 2 }}>
                {shipmentDetails?.shipmentNotes}
              </PDFText>
              <PDFText style={{ ...styles.tableCol, marginVertical: 2 }}>
                {shipmentDetails?.detailNotes}
              </PDFText>
            </View>
          </PDFRow>
          <PDFRow>
            <View style={{ width: "70%" }}>
              <View style={styles.cellPadding}>
                <PDFText style={styles.tableHeader}>
                  21 Established in . on .
                </PDFText>
                <View style={{ marginVertical: 3 }}>
                  <PDFText style={styles.tableCol}>
                    Place: {origin.stateName}, {origin.countryName}
                  </PDFText>
                  <PDFText style={styles.tableCol}>
                    Date:{" "}
                    {formateWithMoment(origin?.requestedDate, "MM-DD-yyyy")}
                  </PDFText>
                </View>
              </View>
              <View style={pdfStyles.borderTop}>
                <PDFRow>
                  <View
                    style={{ height: 60, flex: 1, ...pdfStyles.borderRight }}
                  >
                    <PDFRow
                      style={{ height: "100%" }}
                      direction={"column"}
                      justify="space-between"
                    >
                      <PDFText
                        style={{ ...styles.tableHeader, ...styles.cellPadding }}
                      >
                        22
                      </PDFText>
                      <PDFText
                        style={{ ...styles.tableHeader, ...styles.cellPadding }}
                      >
                        Signature and stamp of the sender
                      </PDFText>
                    </PDFRow>
                  </View>
                  <View style={{ height: 60, flex: 1 }}>
                    <PDFRow
                      style={{ height: "100%" }}
                      direction={"column"}
                      justify="space-between"
                    >
                      <PDFText
                        style={{ ...styles.tableHeader, ...styles.cellPadding }}
                      >
                        23
                      </PDFText>
                      <PDFText
                        style={{ ...styles.tableHeader, ...styles.cellPadding }}
                      >
                        Signature and stamp of the carrier
                      </PDFText>
                    </PDFRow>
                  </View>
                </PDFRow>
              </View>
            </View>
            <View style={{ width: "30%", ...pdfStyles.borderLeft }}>
              <View style={{ height: 60, flex: 1 }}>
                <PDFRow
                  style={{ height: "100%" }}
                  direction={"column"}
                  justify="space-between"
                >
                  <PDFRow justify="space-between">
                    <View style={styles.cellPadding}>
                      <PDFText style={styles.tableHeader}>
                        24 Goods received
                      </PDFText>
                      <PDFText
                        style={{
                          ...styles.tableCol,
                        }}
                      >
                        {inputData?.goodReceived}
                      </PDFText>
                    </View>
                    <View style={styles.cellPadding}>
                      <PDFText style={styles.tableHeader}>25 Date</PDFText>
                      <PDFText
                        style={{
                          ...styles.tableCol,
                        }}
                      >
                        {formateWithMoment(origin?.actualDate, "MM-DD-yyyy")}
                      </PDFText>
                    </View>
                  </PDFRow>
                  <PDFText
                    style={{ ...styles.tableHeader, ...styles.cellPadding }}
                  >
                    Signature and stamp of the consignee
                  </PDFText>
                </PDFRow>
              </View>
            </View>
          </PDFRow>
        </View>
        <View
          style={{
            ...styles.tableCol,
            // transform: "rotate(270deg) translateX(5%)",
            transform: "rotate(270deg)",
            transformOrigin: "left",
            position: "absolute",
            left: 5,
            bottom: -6,
          }}
          fixed={true}
          render={({ pageNumber }) => {
            if (pageNumber === 1) {
              return (
                <PDFText style={{ fontSize: 9 }}>
                  To be completed on the senders responsibility
                </PDFText>
              );
            }
            return <Fragment></Fragment>;
          }}
        />

        <View
          style={{
            ...styles.tableCol,
            // transform: "rotate(270deg) translateX(5%)",
            transform: "rotate(270deg)",
            transformOrigin: "left",
            position: "absolute",
            left: 5,
            bottom: "45%",
          }}
          fixed={true}
          render={({ pageNumber }) => {
            if (pageNumber === 1) {
              return (
                <PDFText style={{ fontSize: 9 }}>
                  framed with heavy lines must be filled in by the carrier.
                </PDFText>
              );
            }
            return <Fragment></Fragment>;
          }}
        />
      </View>
    </PDFPage>
  );
};

const styles = StyleSheet.create({
  header: {},
  title: { fontSize: 16, fontWeight: "700", marginBottom: 2 },
  headerRow: {
    justifyContent: "space-between",
    alignItems: "center",
  },
  section: {
    paddingHorizontal: 5,
    paddingVertical: 3,
  },
  imageSection: {
    width: "100%",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    paddingVertical: 10,
  },
  image: {
    width: "60%",
  },
  tableHeader: {
    fontWeight: "700",
    fontSize: 8,
    paddingBottom: 1,
  },
  tableCol: {
    fontSize: 7,
  },
  cellPadding: {
    paddingHorizontal: 6,
    paddingVertical: 2,
  },
});

export default CMRPrintDocument;
