import { pdf } from "@react-pdf/renderer";
import PDF from "./PDF";

const generateCMRPDF = async (props) => {
  console.log("started CMR");
  const data = JSON.parse(props);
  const result = await pdf(<PDF {...data} />).toBlob();
  console.log("ended CMR");
  return result;
};

export default generateCMRPDF;
