import { pdf } from "@react-pdf/renderer";
import PDF from "./PDF";

const generateCIPDF = async (props) => {
  console.log("started CI");
  const data = JSON.parse(props);
  console.log("props", data);
  const result = await pdf(<PDF ciData={data} />).toBlob();
  console.log("ended CI");
  return result;
};

export default generateCIPDF;
