export const getPrimaryMenu = (menus) => {
  if (menus && menus.length > 0) {
    let parentMenu = menus.filter(({ parentID }) => !parentID);
    parentMenu = parentMenu.map((row) => ({
      ...row,
      subMenus: menus.filter(({ parentID }) => parentID === row.navigationID),
    }));
    menus = parentMenu;
  } else {
    menus = [];
  }

  return [
    ...menus,
    {
      navigationID: 60,
      menuName: "Settings",
      menuText: "Settings",
      parentID: 6,
      subMenus: [
        // {
        //   menuName: "CompanySettings",
        //   menuText: "Company Settings",
        // },
        {
          menuName: "TMSSettings",
          menuText: "TMS Settings",
        },
      ],
    },
  ].map((row) => ({ ...row, hasSubMenu: Boolean(row?.subMenus?.length) }));
};

