export const triggerNotification = (API = "", payload) => {
  const {
    type = NOTIFICATION_TYPES.SUCCESS,
    key = "mainKey",
    title = "Title",
    placement = "bottomRight",
  } = payload;
  if (API)
    API.open({
      key,
      message: title,
      placement,
      closeIcon: <></>,
      icon: <></>,
      duration: 4,
      className: `custom-notification ${type}`,
    });

  (() => {
    setTimeout(() => {
      if (API)
        API.open({
          key,
          message: title,
          placement,
          closeIcon: <></>,
          icon: <></>,
          duration: 2,
          className: `custom-notification ${type}`,
        });
    }, 1000);
  })();
};

