export const calculateCube = (length, width, height, qty, unit) => {
  length = stringToNumber(length);
  width = stringToNumber(width);
  height = stringToNumber(height);
  qty = stringToNumber(qty);
  let cube = length * width * height;
  if (cube == 0) {
    return cube.toString();
  }
  if (unit === DIM_UOM_TYPES.CM || unit === "CM") {
    let n = +cube / 1000000;
    n = (Math.round(n * 1000) / 1000).toString();
    if (n == 0) {
      return (Math.round(0.001 * qty * 1000) / 1000).toString();
    }
    return (Math.round(n * qty * 1000) / 1000).toString();
  } else if (unit === DIM_UOM_TYPES.INCHES || unit === "IN") {
    let n = +cube / 1728;
    n = (Math.round(n * 1000) / 1000).toString();
    if (n == 0) {
      return (Math.round(0.001 * qty * 1000) / 1000).toString();
    }
    return (Math.round(n * qty * 1000) / 1000).toString();
  } else {
    return (Math.round(0.001 * qty * 1000) / 1000).toString();
  }
};

