export const getEmailMask = (email) => {
  if (!email) {
    return "";
  }
  email = email.split("");
  let finalArr = [];
  let len = email.indexOf("@");
  email.forEach((item, pos) => {
    pos >= 2 && pos <= len - 3 ? finalArr.push("*") : finalArr.push(email[pos]);
  });
  return finalArr.join("");
};

