export const filterRateShopAdditionalFields = (
  data,
  filterData,
  accessor = "codeDesc"
) => {
  if (filterData.filterType === "START") {
    if (filterData?.searchValue && filterData?.searchValue?.length > 0) {
      const regex = new RegExp(`^${filterData?.searchValue}`, "gi");
      data = sortBy(data, [
        (row) => !regex.test(row[accessor]),
        (row) => !regex.test(row[accessor]),
      ]);
    } else {
      data = sortBy(data, [""], ["asc"]);
    }
    data = data?.map((row) => ({
      ...row,
      hide: true,
    }));
  } else {
    const originIndex = findIndex(data, {
      [accessor]: String(filterData?.searchValue).toUpperCase(),
    });

    if (originIndex >= 0) {
      data.splice(originIndex, 1);
      data = [{ ...data[originIndex] }, ...data];

      data = data?.map((row) => ({
        ...row,
        hide: true,
      }));
    } else {
      data = data?.map((row) => ({
        ...row,
        hide: false,
      }));
    }
  }
  return data;
};

export const filterRateShopAdditionalFieldsVersionTwo = (
  data,
  filterData,
  accessor = "codeDesc"
) => {
  let filteredList = [];
  let listForClearState = [];
  const selectedList = filter(data, { isChecked: true });
  if (filterData?.searchValue && filterData?.searchValue?.length > 0) {
    if (filterData.filterType === "START") {
      const regex = new RegExp(`^${filterData?.searchValue}`, "gi");
      filteredList = filter(
        data,
        ({ [accessor]: recordValue }) =>
          regex.test(recordValue) || regex.test(recordValue)
      );
    } else {
      filteredList = filter(
        data,
        (row) => row[accessor] === filterData.searchValue
      );
    }
  } else {
    listForClearState = filter(
      data,
      ({ lookupKey }) => findIndex(selectedList, { lookupKey }) === -1
    );
  }
  filteredList = filter(
    filteredList,
    ({ lookupKey }) => findIndex(selectedList, { lookupKey }) === -1
  );
  return [...filteredList, ...selectedList, ...listForClearState];
};

export const rgbaToHex = (rgba) => rgbHex(rgba?.r, rgba?.g, rgba?.b, rgba?.a);

// Need to enable this feature for admin users only later
const omitCssVariables = [
  "header",
  "topMenuText",
  "dark-1",
  "bottomMenuText",
  "font-family",
];
export const applyTheme = (cssVariables) => {
  const rootElement = document.documentElement;
  if (typeof cssVariables === "object") {
    for (const key in cssVariables) {
      if (Object.hasOwnProperty.call(cssVariables, key)) {
        if (!omitCssVariables.includes(key)) {
          rootElement.style.setProperty(`--${key}`, cssVariables[key]);
        }
      }
    }
  }
};

