export const hyphenOneToEmptyInObject = (object) => {
  if (object) {
    if (typeof object === "object") {
      for (const key in object) {
        if (Object.hasOwnProperty.call(object, key)) {
          if (object[key] == "-1") {
            object[key] = "";
          }
        }
      }
    }
  }
  return object;
};

