export const generateStopNumbers = (n = 0, stopType) => {
  let options = [];

  if (typeof n === "number") {
    for (let i = 1; i <= n; i++) {
      if (stopType == "2" && i === 1) continue;
      options.push({ value: i, label: i });
    }
  }

  return options;
};

export const arrayMove = (arr, fromIndex, toIndex) => {
  let localArray = cloneDeep(arr);
  var element = localArray[fromIndex];
  localArray.splice(fromIndex, 1);
  localArray.splice(toIndex, 0, element);
  return localArray;
};

