export const diffInVariantFields = (obj1, obj2) => {
  const diffInFields = Object.entries(obj2).filter(
    ([field, obj2Value]) => obj1[field] !== obj2Value
  );
  return diffInFields;
};

export const downloadBlobFile = (blob, fileName, isBlob = true) => {
  let link = document.createElement("a");
  const url = isBlob ? window.URL.createObjectURL(blob) : blob;
  link.href = url;
  link.setAttribute("download", fileName);
  document.body.appendChild(link);
  console.log("Before Click", link);
  link.click();
  console.log("After Click", link);
  setTimeout(function () {
    document.body.removeChild(link);
    window.URL.revokeObjectURL(url);
  }, 1000);
};
