import { pdf } from "@react-pdf/renderer";
import PDF from "./PDF";

const generateSEDPDF = async (props) => {
  console.log("started SED");
  const data = JSON.parse(props);
  console.log("props", data);
  const result = await pdf(<PDF {...data} />).toBlob();
  console.log("ended SED");
  return result;
};

export default generateSEDPDF;
