import { filter, find } from "lodash";
import { shipmentRateShopLocalToRateEngineAPI } from "../../../../../../../utilz/shipmentDataManupulation";

const constructDataForRateEngine = async (props) => {
  console.log("started data construction for RateEngine");
  const {
    shipmentDetailsLocal,
    filterData,
    shipmentBranches,
    accountingFormData,
    havingAccessForMenu,
    isAPIRateShop,
  } = JSON.parse(props);
  // console.log("props", props);
  let accessorials = [];
  if (accountingFormData?.accessorials) {
    accessorials = filter(
      accountingFormData?.accessorials,
      havingAccessForMenu
        ? {
            isChecked: true,
          }
        : {}
    )?.map((row) => ({
      AccessorialCode: row.accChargeType?.trim(),
      Description: row.codeDesc,
      BilledAmount: 0,
      CurrencyCode: null,
    }));
  }

  const branchDetails = find(shipmentBranches.data, {
    branchKey: shipmentDetailsLocal.branchKey,
  });
  filterData.cargoType = filterData.cargoType?.length
    ? filterData.cargoType.join(",")
    : "";
  let formData = shipmentRateShopLocalToRateEngineAPI(
    accessorials,
    shipmentDetailsLocal,
    filterData,
    branchDetails?.currencyCode ? branchDetails?.currencyCode : "**",
    isAPIRateShop
  );
  formData.isNeedToSave = true;
  formData.isAPIRateShop = isAPIRateShop;
  // formData.carrierKey = String(formData.carrierKey);
  console.log("ended data construction for RateEngine");
  return formData;
};

export default constructDataForRateEngine;
