import { pdf } from "@react-pdf/renderer";
import PDF from "./PDF";

const generateBOLPDF = async (props) => {
  console.log("started BOL");
  const data = JSON.parse(props);
  console.log("props", data);
  const result = await pdf(<PDF {...data} />).toBlob();
  console.log("ended BOL");
  return result;
};

export default generateBOLPDF;
